/*   Typography    */
@font-face {
  font-family: "GothamLight";
  src: url('"../fonts/gotham/GothamLight.eot');
  src: url("../fonts/gotham/GothamLight.eot") format("embedded-opentype"), url("../fonts/gotham/GothamLight.woff2") format("woff2"), url("../fonts/gotham/GothamLight.woff") format("woff"), url("../fonts/gotham/GothamLight.ttf") format("truetype"), url("../fonts/gotham/GothamLight.svg#GothamLight") format("svg");
}
body, h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6, p, .navbar, .brand, .btn-simple, .alert, a, .td-name, td, button.close {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: "Montserrat", sans-serif;
}

body {
  font-size: 13px;
}

a {
  color: #393950;
}
a:hover, a:focus {
  color: #3eabe2;
  text-decoration: none;
}

h1,
.h1 {
  font-size: 1.725rem;
  font-family: "GothamLight";
}

h2,
.h2 {
  font-size: 25px;
  line-height: 31px;
  text-transform: uppercase;
  letter-spacing: 8px;
  color: #393950;
  font-weight: 700;
  margin-bottom: 20px;
}

h3,
.h3 {
  font-size: 21px;
  line-height: 27px;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #393950;
  font-weight: 700;
  margin-bottom: 20px;
}

h4,
.h4 {
  font-size: 22px;
  line-height: 27px;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #393950;
  font-weight: 700;
  margin-bottom: 20px;
}

h5,
.h5 {
  font-size: 18px;
  line-height: 27px;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #393950;
  font-weight: 700;
  margin-bottom: 20px;
}

h6,
.h6 {
  font-size: 16px;
  font-weight: 700;
}

p {
  font-size: 12px;
  line-height: 1.82;
  margin-bottom: 1rem;
  word-break: break-word;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  color: #393950;
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a, .h1 a, .h2 a, .h3 a, .h4 a, .h5 a, .h6 a {
  color: inherit;
}

small, .small {
  font-size: 13px;
}

b, .b, strong, .strong {
  font-weight: 700;
}

.page-pretitle {
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #95aac9;
}

.page-title {
  font-size: 23px;
  font-weight: 700;
  color: #393950;
  line-height: 30px;
  margin-bottom: 20px;
}

.page-category {
  color: #444444;
  line-height: 1.8;
  margin-bottom: 25px;
}

.text-primary, .text-primary a {
  color: #3eabe2 !important;
}
.text-primary:hover, .text-primary a:hover {
  color: #3eabe2 !important;
}

.text-secondary, .text-secondary a {
  color: #393950 !important;
}
.text-secondary:hover, .text-secondary a:hover {
  color: #393950 !important;
}

.text-info, .text-info a {
  color: #48ABF7 !important;
}
.text-info:hover, .text-info a:hover {
  color: #48ABF7 !important;
}

.text-success, .text-success a {
  color: #31CE36 !important;
}
.text-success:hover, .text-success a:hover {
  color: #31CE36 !important;
}

.text-warning, .text-warning a {
  color: #FFAD46 !important;
}
.text-warning:hover, .text-warning a:hover {
  color: #FFAD46 !important;
}

.text-danger, .text-danger a {
  color: #F25961 !important;
}
.text-danger:hover, .text-danger a:hover {
  color: #F25961 !important;
}

label {
  color: #495057 !important;
  font-size: 14px !important;
}

.text-small {
  font-size: 11px;
}

.metric-value {
  margin-bottom: 5px;
  line-height: 1;
  white-space: nowrap;
}

.metric-label {
  font-size: 0.975rem;
  font-weight: 500;
  color: #686f76;
  white-space: nowrap;
  margin-bottom: 0;
}

/*   Font-weight    */
.fw-light {
  font-weight: 300 !important;
}

.fw-normal {
  font-weight: 400 !important;
}

.fw-mediumbold {
  font-weight: 400 !important;
}

.fw-bold {
  font-weight: 700 !important;
}

.fw-extrabold {
  font-weight: 900 !important;
}

