.timeline-order {
  position: relative;
  margin: 40px 0 0 0;
  padding: 0;
}
.timeline-order:before {
  content: "";
  background: #dee2e6;
  height: 2px;
  width: 75%;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  top: 16px;
  z-index: 0;
}
.timeline-order:after {
  content: "";
  background: #31CE36;
  height: 2px;
  left: 12.5%;
  margin: auto;
  position: absolute;
  top: 16px;
  z-index: 0;
}
.timeline-order .checkpoint {
  width: 25%;
  float: left;
  text-align: center;
}
.timeline-order .checkpoint .dots {
  width: 34px;
  height: 34px;
  background-image: url(/images/timeline-inactive.png);
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-block;
  margin: auto;
  z-index: 55;
  position: relative;
}
.timeline-order .checkpoint.finished {
  position: relative;
}
.timeline-order .checkpoint.finished .dots {
  background-image: url(/images/timeline-tick-finish.png);
}
.timeline-order .checkpoint.finished:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #31CE36;
  top: 16px;
  right: 50%;
}
.timeline-order .checkpoint:first-child.finished:before, .timeline-order .checkpoint:first-child.active:before {
  display: none;
}
.timeline-order .checkpoint.active {
  position: relative;
}
.timeline-order .checkpoint.active .dots {
  background-image: url(/images/timeline-tick-active.png);
}
.timeline-order .checkpoint.active:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #31CE36;
  top: 16px;
  right: 50%;
}
.timeline-order .checkpoint .details {
  margin-top: 20px;
}
.timeline-order .checkpoint .details span {
  font-size: 14px;
  text-transform: uppercase;
  color: #171e27;
  display: block;
  font-weight: 700;
}

